/*FONTS*/
@font-face {
    font-family: 'MontserratThin';
    src: url('../../common/fonts/montserrat-thin-webfont.eot');
    src: local('0'), url('../../common/fonts/montserrat-thin-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../common/fonts/montserrat-thin-webfont.woff2') format('woff2'),
    url('../../common/fonts/montserrat-thin-webfont.woff') format('woff'),
    url('../../common/fonts/montserrat-thin-webfont.svg#montserratthin') format('svg');
    font-weight: 100;
    font-style: normal;
}
@font-face {
    font-family: 'MontserratLight';
    src: url('../../common/fonts/montserrat-light-webfont.eot');
    src: local('0'), url('../../common/fonts/montserrat-light-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../common/fonts/montserrat-light-webfont.woff2') format('woff2'),
    url('../../common/fonts/montserrat-light-webfont.woff') format('woff'),
    url('../../common/fonts/montserrat-light-webfont.svg#montserratlight') format('svg');
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'MontserratMedium';
    src: url('../../common/fonts/montserrat-medium-webfont.eot');
    src: local('0'), url('../../common/fonts/montserrat-medium-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../common/fonts/montserrat-medium-webfont.woff2') format('woff2'),
    url('../../common/fonts/montserrat-medium-webfont.woff') format('woff'),
    url('../../common/fonts/montserrat-medium-webfont.svg#montserratmedium') format('svg');
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: 'MontserratBold';
    src: url('../../common/fonts/montserrat-bold-webfont.eot');
    src: local('0'), url('../../common/fonts/montserrat-bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../common/fonts/montserrat-bold-webfont.woff2') format('woff2'),
    url('../../common/fonts/montserrat-bold-webfont.woff') format('woff'),
    url('../../common/fonts/montserrat-bold-webfont.svg#montserratbold') format('svg');
    font-weight: 700;
    font-style: normal;

}
@import "../../common/sass/variables";

* {
    font-family: MontserratLight, serif;
    box-sizing: border-box;
    color: $lightTextColor;
    margin: 0;
    font-size: 16px;
}

a {
    text-decoration: none;
}

.appWrapper {
    background-color: $BgColor;
    position: relative;
    .app {
        max-width: 1240px;
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        margin: 0 auto;
        padding: 0 15px;
    }
}